*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
body{
  background-color: rgb(43, 43, 43)
}
.App{
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.button-body{
  width: 20vw;
  margin: 40px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(204, 206, 206);
  box-shadow: 1px 0px 5px 1px black;
  border-radius: 5px;
}
.title{
  font-size: 20px;
  margin-top: 5px;
}
.buttons{
  margin: 20px;
  display: grid;
  grid-template-columns: repeat(4, 2fr);
  gap: 10px;
}
.display{
  grid-column: span 4;
  /* width: 15vw; */
  height: 8vh;
  padding: 10px;
  border-style: none;
  border-radius: 5px;
  background-color: rgb(0, 0, 0);
  color: white;
  font-size: 20px;
  box-shadow: 1px 0px 2px 1px black;
}
.operator, .number{
  width: 4vw;
  height: 6vh;
  padding: 8px;
  border-radius: 5px;
  outline: none;
  font-weight: 600;
}
.operator:hover, .number:hover{
  background-color: rgb(248, 248, 248);
  box-shadow: 1px 0px 0px 1px black;
  transform: translateY(0.7px);

}
.equals{
  background-color: rgb(241, 241, 151);
  border-radius: 5px;
  
}
.equals:hover{
  box-shadow: 2px 2px 1px 1px black;
  transform: translateY(0.5px);
}
.display::placeholder {
  color: aquamarine;
}


@media screen and (max-width: 576px) {
