  }
  .title{
    font-size: 20px;
    margin-top: 5px;
  }
  .buttons{
    margin: 20px;
    display: grid;
    grid-template-columns: repeat(4, 2fr);
    gap: 10px;
  }
  .display{
    grid-column: span 4;
    /* width: 15vw; */
    height: 8vh;
    padding: 10px;
    border-style: none;
    border-radius: 5px;
    background-color: rgb(92, 92, 93);
    color: white;
    font-size: 20px;
    box-shadow: 1px 0px 2px 1px black;
  }
  .operator, .number{
    width: 20vw;
    height: 6.4vh;
    padding: 8px;
    border-radius: 5px;
  }
  .operator:hover, .number:hover{
    background-color: rgb(195, 225, 255);
